.home {
    overflow-x: hidden;

    .home-main {
        height: 100vh;

        .main {
            width: 100vw;
            height: 100vh;
            object-fit: cover;
        }

        .main-description {
            position: absolute;
            top: 90%;
            left: 50%;
            transform: translate(-50%, -50%);
            padding: 20px;
            text-align: center;
        }
    }

    .home-product {
        min-height: 100vh;
        padding: 100px 10px 0px 10px;
    }

    .home-story {
        min-height: 100vh;
    }

    .home-contact {
        min-height: 100vh;
    }
}