$primary: #262626;

@import "../pages/Home/home.scss";

header {
    position: fixed;
    display: flex;
    justify-content: space-between;

    button {
        margin: 0px 20px !important;
    }

    .nav-start {
        width: 33vw;
        padding: 10px;
    }

    .nav-center {
        width: 34vw;
        padding: 10px;
        text-align: center;
    }

    .nav-end {
        width: 33vw;
        padding: 10px;
        text-align: right;
    }
}